#calculator {
    width: 530px;
    padding: 2rem;
    border-radius: 8px;
    background-color: black;
    box-shadow: 10px 10px var(--greyish);
}

#input {
    display: flex;
    flex-direction: column;
    gap: 15px;
    font-family: "Courier New";
    text-align: right;
    margin-bottom: 50px;
    word-wrap: break-word;
}

#total-value {
    min-height: 35.5938px;
    padding: 4.8px 8px;
    border-radius: 3px;
    font-size: 22.4px;
    color: white;
    background-color: orange;
    text-align: center;
    font-weight: bold;
}

#display {
    width: 100%;
    min-height: 65px;
    padding: 8px;
    border-radius: 5px;
    font-size: 40px;
    color: orange;
    background-color: var(--greyish);
}

#buttons {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
}

#clear, #equals {
    grid-column: span 2;
}

.box {
    padding: 1.5rem;
    border: none;
    font-family: "Courier New";
    font-weight: bold;
    font-size: 3rem;
    background-color: var(--red);
    cursor: pointer;
    margin: 0.2rem;
}

.box:nth-of-type(1), .box:nth-of-type(2) {
    background-color: var(--yellow);
}

.box:nth-of-type(3) {
    grid-row: 5 / 6;
    grid-column: 2 / 3;
}

.box:nth-of-type(13) {
    grid-row: 1 / 2;
    grid-column: 4 / 5;
    background-color: var(--lightblue);
}

.box:nth-of-type(14) {
    grid-row: 2 / 3;
    grid-column: 4 / 5;
    background-color: var(--lightblue);
}

.box:nth-of-type(15) {
    grid-row: 3 / 4;
    grid-column: 4 / 5;
    background-color: var(--lightblue);
}

.box:nth-of-type(16) {
    grid-row: 4 / 5;
    grid-column: 4 / 5;
    background-color: var(--lightblue);
}

.box:nth-of-type(17) {
    background-color: var(--lightblue);
}

.box:last-of-type {
    background-color: var(--green);
}

.box:is(:hover) {
    background-color: var(--darkblue);
    color: white;
    outline: none;
}

@media (max-width: 530px) {
    html {
        font-size: 12px;
    }

    #calculator {
        width: 400px;
    }
}