* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    --greyish: hsl(0, 0%, 50%);
    --yellow: hsl(44, 73%, 75%);
    --red: hsl(0, 58%, 61%);
    --lightblue: hsl(195, 53%, 79%);
    --green: hsl(140, 58%, 61%);
    --darkblue: hsl(240, 100%, 27%);
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: hsl(0, 0%, 90%);
    user-select: none;
}